import React from "react"
class About extends React.Component{
render(){
    return(
<section className="colorlib-about" data-section="about">
        <div className="colorlib-narrow-content">
          <div className="row">
            <div className="col-md-12">
              <div className="row row-bottom-padded-sm animate-box" data-animate-effect="fadeInLeft">
                <div className="col-md-12">
                  <div className="about-desc">
                    <span className="heading-meta">About Us</span>
                    <h2 className="colorlib-heading">Who Am I?</h2>
                    <p><strong>Hi I'm Sanu Dutta</strong>, As a Web Developer I always try to build a website which is user friendly with responsive layout and reusable components so that I can put flexibility and avoiding repetition of codes. </p>
                    <p>I am a techno enthusiastic person who try to learn new technology that facinate me and always try to adopt myself to the fast changing environment to cope with the changing world so that I can make a difference. </p>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-md-3 animate-box" data-animate-effect="fadeInLeft">
                  <div className="services color-1">
                    <span className="icon2"><i className="icon-bulb" /></span>
                    <h3>Data Science</h3>
                  </div>
                </div>
                <div className="col-md-3 animate-box" data-animate-effect="fadeInRight">
                  <div className="services color-2">
                    <span className="icon2"><i className="icon-globe-outline" /></span>
                    <h3>Web Developer</h3>
                  </div>
                </div>
                <div className="col-md-3 animate-box" data-animate-effect="fadeInTop">
                  <div className="services color-3">
                    <span className="icon2"><i className="icon-data" /></span>
                    <h3>Software</h3>
                  </div>
                </div>
                <div className="col-md-3 animate-box" data-animate-effect="fadeInBottom">
                  <div className="services color-4">
                    <span className="icon2"><i className="icon-phone3" /></span>
                    <h3>Application</h3>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      );
    }
}
export default About